 .text-center {
    font-family: filicudi-solid, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 70px;
    color: rgb(254, 217, 8);
    text-align: center;
  }

  .para-center {
    text-align: center;
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
    color: black;
  }

  section {
    padding: 5rem 0;
  }
  
  .card-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .card {
    width: 37rem;
    background-color: #ffffff;
    display: flex;
    flex-direction: row;
    
    margin: 1rem 0;
    box-shadow: .5rem .5rem 3rem rgba(0,0,0,0.2);
  
  }
  
  .card2 {
    width: 29rem;
    height: 25rem;
    background-color: #ffffff;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 1rem 0;
    box-shadow: .5rem .5rem 3rem rgba(0,0,0,0.2);
  }
  
  @media screen and (min-width: 700px){
    .card-wrapper{
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
  
    .card {
        margin: 1rem;
    }
  
    @keyframes fadeIn {
        from{
            opacity: 0;
        }
        to{
            opacity: 1;
        }
    }
    .card:nth-child(1){
            animation: fadeIn .5s .5s backwards;
        }
    .card:nth-child(2){
        animation: fadeIn .5s .5s backwards;
    }
    .card:nth-child(3){
        animation: fadeIn .5s .5s backwards;
    }
  }
  
  
  .image {
    display: block;
    width: 100%;
    height: auto;
  }
  
  .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: rgb(255, 255, 255);
  }
  
  .overlay:hover{
    opacity: 1;
  }
  
  .logo-image {
    height: 20%;
    width: 20%;
  }
  
  .text {
    color: black;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
  }

  .subtext {
    color: black;
    font-size: 10px;
    text-align: center;
  }